<template>
  <section class="wrap wrap_self">
    <div class="act_header act_header--throne play_on">
      <div class="act_header__con">
        <div class="act_header__bg header_throne__bg1"></div>
        <div class="act_header__bg header_throne__bg2"></div>
        <div class="act_header__bg header_throne__bg3"></div>
      </div>
      <h1 class="act_header__tit">资格赛 - 年度盛典</h1>
      <a class="act_header__rule">
        <span class="btn_rule" @click="handleJumpRule">活动规则</span>
      </a>
      <Headers v-if="activityInfo" :info="activityInfo" />
    </div>
    <Nav @change="handleChange" />
    <div class="box_main box_main__star">
      <div class="box_main__bg">
        <div class="box_main__repeat"></div>
      </div>
      <div class="box_main__con">
        <div class="tit_main">
          <div class="tit_main__con">
            <span class="icon icon_tit_l"></span>
            <h2 class="tit_main__text">本组排名</h2>
            <span class="icon icon_tit_r"></span>
          </div>
          <p class="tit_main__desc">
            每组前<strong>200名</strong>且闪耀值超过2000的主播
          </p>
          <p class="tit_main__desc">可入围年度盛典个人赛正式赛</p>
        </div>
        <Top3Rank :list="top3Rank" />
        <RankList :info="activityInfo" :list="rankList" :hasMore="hasMore" />
      </div>
    </div>
  </section>
</template>

<script>
import { getCookie } from '@/utils/cookie';
import Headers from './../../components/qualification/Headers';
import Nav from './../../components/qualification/Nav';
import Top3Rank from './../../components/qualification/Top3Rank';
import RankList from './../../components/qualification/RankList';

import {
  getActivityInfo,
  getAnchorInfo,
  getAnchorRankList
} from './../../common/request';
export default {
  name: 'Qualification',
  data () {
    return {
      activityInfo: null,
      pos: 1,
      strPassBack: '',
      list: [],
      timer: null, // 定时器
      hasMore: true // 是否有下一页
    };
  },
  watch: {
    '$route.query': {
      immediate: true,
      handler (value) {
        if (!value) return;
        if (Number(value.pos) === this.pos) return;
        let pos = Number(value.pos);
        if (!pos) {
          pos = ((Math.random() * 3) >>> 0) + 1;
          this.$router.replace({
            name: 'qualification',
            query: {
              ...this.$route.query,
              pos
            }
          });
        }
        this.pos = pos;
      }
    }
  },
  components: {
    Headers,
    Nav,
    Top3Rank,
    RankList
  },
  computed: {
    top3Rank () {
      let max = 3;
      let min = this.activityInfo?.stActivityRule.uMinStartValue || 10000;
      let list = this.list.slice(0, 3).filter(item => {
        // 前3名大于10000晋级
        return item.uStarValue > min;
      });
      while (max - list.length > 0) {
        list.push({ stAnchorBasicInfo: { strNick: '虚位以待' } });
      }
      return list;
    },
    specialRank () {
      let max = 3;
      let list = this.list.slice(0, 3).filter(item => {
        // 前3名大于10000晋级
        return item.uStarValue > 10000;
      });
      return this.list.slice(list.length, max);
    },
    rankList () {
      let list = this.list.slice(3);
      let min = this.activityInfo?.stActivityRule.uMinStartValue || 10000;
      let top3List = this.list.slice(0, 3).filter(item => {
        // 前3名大于10000晋级
        return item.uStarValue <= min;
      });
      return top3List.concat(list);
    }
  },
  async mounted () {
    await this.getData();
    window.addEventListener('scroll', this.handleScroll, true);
  },
  methods: {
    async getData () {
      if (this.$route.query.from === '2') {
        // 分享卡片跳转
        await this.getAnchorInfo();
      }
      const resData = await getActivityInfo({
        uAnchorId: Number(getCookie('uid'))
      });
      if (resData.isWebappSuccess) {
        if (resData.uActStatus === 2) {
          // 结算中
          this.$toast.show({
            text: '榜单结算中，请稍候',
            duration: 1500
          });
        }
        this.activityInfo = resData;
        await this.getRankListData();
      }
    },
    async getAnchorInfo () {
      const anchorid = this.$route.query.anchorid || getCookie('uid');
      if (!anchorid) return;
      const resData = await getAnchorInfo({
        uAnchorId: Number(anchorid)
      });
      if (resData.isWebappSuccess) {
        const info = resData?.stAnchorBasicInfo;
        if (info.iLiveStatus) {
          // 分享页跳转榜单，主播开播跳转直播间
          this.$handleAvatar({
            bInLiving: Boolean(info.iLiveStatus),
            strRoomId: info.strRoomId,
            uAnchorId: info.uAnchorId
          });
        }
      }
    },
    async getRankListData () {
      try {
        console.log('请求前', this.pos);
        let curPos = this.pos;
        const resData = await getAnchorRankList({
          uGroupId: this.pos,
          strPassBack: this.strPassBack || '',
          uPageGetNum: 10
        });
        if (!resData.isWebappSuccess || curPos !== this.pos) return;
        this.strPassBack = resData.strPassBack;
        this.hasMore = resData.bHasMore;
        this.list = this.$filterMultiValue(this.list, resData.vecRankItem, [
          'stAnchorBasicInfo',
          'uAnchorId'
        ]);
      } catch (err) {
        this.hasMore = true;
        console.err(err);
      }
    },
    handleJumpRule () {
      location.replace(
        'https://kg.qq.com/gtimg/music/common/upload/kgvisual/T-G0PSa724/index.html'
      );
      // this.$router.push({
      //   name: RACE_QUALIFICATION_RULE,
      //   query: {
      //     ...this.$route.query
      //   }
      // });
    },
    handleChange (pos) {
      this.pos = pos;
      this.clear();
      this.getRankListData();
    },
    handleScroll () {
      this.timer = this.$handleScroll(this.timer, () => {
        if (this.hasMore) {
          // 下一页
          this.getRankListData();
        }
      });
    },
    clear () {
      this.strPassBack = '';
      this.list = [];
    }
  }
};
</script>
