<template>
  <div class="box_media">
    <ul class="qui_media qui_media--star">
      <RankItem
        v-for="(item, index) in normalList"
        :key="item.stAnchorBasicInfo.uAnchorId"
        :rank="3 + index + 1"
        :info="item"
      />
    </ul>
    <div v-if="dangerList.length">
      <div class="tit_end" v-if="info && info.uActStatus === 1">
        <span class="icon icon_end_l"></span>
        <h2 class="tit_end__text">以下主播已淘汰</h2>
        <span class="icon icon_end_r"></span>
      </div>
      <div class="tit_danger" v-else>
        <h3>危险区</h3>
      </div>
    </div>
    <ul class="qui_media qui_media--star">
      <RankItem
        v-for="(item, index) in dangerList"
        :key="item.stAnchorBasicInfo.uAnchorId"
        :rank="3 + normalList.length + index + 1"
        :info="item"
      />
    </ul>
    <a class="mod_more" v-if="list.length">
      <span class="icon icon_more_l"></span>
      <span class="mod_more__info">{{
        hasMore ? '加载更多' : '已经到底了'
      }}</span>
      <span class="icon icon_more_r"></span>
    </a>
  </div>
</template>

<script>
import RankItem from './RankItem';
export default {
  name: 'RankList',
  props: {
    list: {
      type: Array,
      default: null
    },
    hasMore: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
      default: null
    }
  },
  components: {
    RankItem
  },
  data () {
    return {
      normalStatus: [0, 2, 3],
      dangerStatus: [1, 4]
    };
  },
  computed: {
    normalList () {
      let list = this.list.filter(item => {
        return this.normalStatus.includes(item.uStatus);
      });
      return list;
    },
    dangerList () {
      let list = this.list.filter(item => {
        return this.dangerStatus.includes(item.uStatus);
      });
      return list;
    }
  }
};
</script>
