import { fetchServerTime } from './api';
import store from '../vuex';
import { getTs } from './util';

let lastRefreshTs = 0;
let serverTimeInited = false;
let timer = null;
let serverTimer = null;
let serverInterval = 3000;
export function initServerTime () {
  if (serverTimeInited) {
    return;
  }
  serverTimeInited = true;
  refreshCurTime();
  updateCurTime();
  updateServerTime();
}

// 拉取后台server时间
async function refreshCurTime () {
  const resData = await fetchServerTime();
  if (
    resData.isWebappSuccess &&
    resData.uCurTs &&
    resData.uCurTs >= store.state.uCurTs
  ) {
    lastRefreshTs = getTs();
    store.commit('setLastServerTs', resData.uCurTs);
  }
  if (resData.uCallInterval) {
    serverInterval = resData.uCallInterval * 1000;
  }
}

// 1s刷新本地时间
function updateCurTime () {
  timer && clearTimeout(timer);
  timer = setTimeout(() => {
    if (store.state.lastServerTs) {
      store.commit(
        'setCurTs',
        getTs() - lastRefreshTs + store.state.lastServerTs
      );
    }
    updateCurTime();
  }, 1000);
}

// 拉取服务端时间
function updateServerTime () {
  serverTimer && clearTimeout(serverTimer);
  serverTimer = setTimeout(() => {
    refreshCurTime();
    // 测试先关闭
    // updateServerTime();
  }, serverInterval);
}
