<template>
  <div class="mod_scroll">
    <nav class="nav_xs">
      <a
        class="nav_xs__item "
        v-for="item in NAV_CLASS_LIST"
        :key="item.value"
        :class="{ 'nav_xs__item--current': pos === item.value }"
        @click="handleChangeNav(item)"
      >
        <span class="nav_xs__desc">{{ item.label }}</span>
      </a>
    </nav>
  </div>
</template>

<script>
const NAV_CLASS_LIST = [
  {
    label: 'A组',
    value: 1
  },
  {
    label: 'B组',
    value: 2
  },
  {
    label: 'C组',
    value: 3
  },
  {
    label: 'D组',
    value: 4
  },
  {
    label: 'E组',
    value: 5
  },
  {
    label: 'F组',
    value: 6
  },
  {
    label: 'G组',
    value: 7
  },
  {
    label: 'H组',
    value: 8
  }
];

export default {
  name: 'Nav',
  data () {
    return {
      NAV_CLASS_LIST
    };
  },
  computed: {
    pos () {
      if (!this.$route.query.pos) {
        this.handleChangeNav({
          value: ((Math.random() * NAV_CLASS_LIST.length) >>> 0) + 1
        });
      }
      this.$nextTick(() => {
        let els = document.getElementsByClassName('nav_xs__item');
        if (!els.length) return;
        let offset = els[0].offsetWidth || 0;
        let target = document.querySelector('.mod_scroll');
        let number = this.pos - 2;
        number = number < 0 ? 0 : number;
        target.scrollTo(offset * number, 0);
      });
      return Number(this.$route.query.pos);
    }
  },
  methods: {
    handleChangeNav (info) {
      this.$router.replace({
        query: {
          ...this.$route.query,
          pos: info.value
        }
      });
      this.$emit('change', info.value);
    }
  }
};
</script>
