//直播公会赛 webapp 模块的api接口
export async function guildBaseApi ({ cmd, cmdName, data }) {
  return $httpPost({
    ns: namespace_guild,
    cmd,
    data,
    noencode: true,
    mapExt: {
      cmdName,
      ...comMapExt_guild
    }
  })
    .then(res => {
      return handleResponse(res, cmd);
    })
    .catch(err => {
      handleError({
        message: '服务繁忙'
      });
      console.error(err);
    });
}
