const PROD_FOLLOW_L5API = {
  modid: 294017,
  cmd: 1245184
};
const EXP_FOLLOW_L5API = {
  modid: 820865,
  cmd: 524288
};
export async function setFollow ({ uid }) {
  let cmd = 'relation.batchfollow';
  return $httpGet({
    ns: 'proto_relation',
    cmd,
    data: {
      lUid: Number(getCookie('uid')),
      'vctFollowList:array': [`${uid}`].join(',')
    },
    useBridge: true,
    mapExt: {
      file: 'relation_webappJce',
      cmdName: 'WebappBatchFollowReq',
      l5api: PROD_FOLLOW_L5API,
      l5api_test1: EXP_FOLLOW_L5API,
      l5api_exp1: EXP_FOLLOW_L5API,
      ip: __DEV__ ? '100.95.138.18' : null,
      port: 12406
    }
  })
    .then(response => {
      return handleResponse(response.res || {}, cmd);
    })
    .catch(err => {
      handleError({
        message: '服务繁忙'
      });
      console.error(err);
    });
}

