/**
 * mas 规则页配置
 * @param data {id: number, key: string }
 */
export const getMasConfig = data => {
  return new Promise(resolve => {
    fetch(
      `https://y.gtimg.cn/music/common/upload/celebration2020/${
        data.id
      }.json?t=${Date.now()}`
    )
      .then(function (response) {
        return response.json();
      })
      .then(res => {
        try {
          resolve(res[data.key]);
        } catch (err) {
          console.log('请求异常', err);
        }
      })
      .catch(err => {
        console.log('请求异常', err);
      });
  });
};

