export const sendAttention = data => {
  if (!$.platform.qmkege) {
    Vue.prototype.$openApp({
      action: 'webview',
      data: {
        url: location.href
      },
      notDownload: true,
      scheme: 'qmkege'
    });
    return;
  }
  return setFollow(data);
};

