<template>
  <div class="mod_countdown" v-if="!actType && time">
    <p class="mod_countdown__desc">倒计时</p>
    <div class="mod_countdown__time">{{ time }}</div>
  </div>
  <div class="act_countdown" v-else-if="time">
    <p class="act_countdown__desc">
      倒计时
      <span class="act_countdown__time">{{ time }}</span>
    </p>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { getCountStr } from './../common/util';
import { initServerTime } from '../common/curTs';
export default {
  name: 'Countdown',
  props: {
    endTs: {
      type: Number,
      default: 0
    },
    onlyHourSec: {
      type: Boolean,
      default: false
    },
    actType: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      time: '',
      timeInterval: 0,
      timer: null
    };
  },
  computed: {
    ...mapState({
      curTs: state => state.uCurTs
    })
  },
  watch: {
    curTs: {
      immediate: true,
      handler (value) {
        if (!value || this.endTs - this.curTs < 0) return;
        this.timeInterval = this.endTs - this.curTs;

        this.$emit('leftTime', this.timeInterval);
        this.time = getCountStr(this.timeInterval, {
          onlyHourSec: this.onlyHourSec
        });
      }
    }
  },
  created () {
    initServerTime();
  }
};
</script>
