<template>
  <li class="qui_media__item">
    <div class="qui_media__box">
      <a
        href="javascript:"
        class="qui_media__photo qui_media__photo--l"
        @click="
          $handleAvatar({
            bInLiving: info.isLive,
            strRoomId: info.strRoomId,
            uAnchorId: info.uid
          })
        "
      >
        <img
          class="qui_media__img"
          v-lazy="{
            src: $getAvatar(info.uid),
            error: $defaultFace,
            loading: $defaultFace
          }"
          alt=""
        />
        <span class="icon_top" :class="getIconStyle(info.index + 1)">{{
          info.index + 1
        }}</span>
        <span class="icon icon_living" v-if="info.isLive"></span>
      </a>
      <div class="qui_media__info">
        <h3 class="qui_media__tit" v-html="$parseEmoji(info.nickName)"></h3>
        <div class="qui_media__badge" v-if="info.strFanbaseName">
          {{ info.strFanbaseName }}
        </div>
        <p class="qui_media__desc">
          <strong>{{ $parseStarValue(info.starValue) || 0 }}</strong>
          {{ unitText }}
        </p>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  name: 'Top3Rank',
  props: {
    info: {
      type: Object,
      default: null
    }
  },
  methods: {
    getIconStyle (rank = 1) {
      return `icon_top${rank}`;
    }
  },
  computed: {
    unitText () {
      return this.info.type === 'fans' ? '名气值' : '闪耀值';
    }
  }
};
</script>
